import { Component, OnInit, ViewChild } from '@angular/core';
import { FormBuilder, FormGroup, Validators } from '@angular/forms';
import { MatTableDataSource } from '@angular/material/table';
import { FormListComponent } from '@fragments';
import { ApiService, SweetAlertService } from '@services';
import { COMMON_FUNCTION, ENDPOINTS, STATUSES, USER_ROLES } from '@shared';
import { ScreenerReviewerPair } from 'app/core/models/screener-reviewer-pair.model';
import Swal from 'sweetalert2';

@Component({
  selector: 'app-screener-reviewer-pair',
  templateUrl: './screener-reviewer-pair.component.html',
  styleUrls: ['./screener-reviewer-pair.component.scss']
})
export class ScreenerReviewerPairComponent implements OnInit {

  @ViewChild(FormListComponent) formListComponent: FormListComponent;
  dataSource: MatTableDataSource<ScreenerReviewerPair>;
  displayedColumns: string[] = ['screenerName', 'reviewerName'];
  screenerList = [];
  reviewerList = [];

  screenerReviewerPair = new ScreenerReviewerPair();
  screenerReviewerPairList = [];

  screenerReviewerPairForm: FormGroup;


  constructor(
    private apiService: ApiService,
    private formBuilder: FormBuilder,
    private sweetAlertService: SweetAlertService,
  ) { }

  ngOnInit() {
    this.getList();
    this.setFormValidator();
    this.getScreenerAndReviewerForList();
  }

  getList(): void {
    this.apiService.findAll(ENDPOINTS.screenerReviewerPairs).subscribe((res: any) => {
      this.screenerReviewerPairList = res.responseData.data;
      this.dataSource = new MatTableDataSource<ScreenerReviewerPair>(res.responseData.data);
      this.dataSource.paginator = this.formListComponent.paginator;
      this.dataSource.sort = this.formListComponent.sort;
    });

    this.getScreenerAndReviewerForList();
  }

  save(): void {
    this.apiService.save(ENDPOINTS.screenerReviewerPairs, this.screenerReviewerPair).subscribe(
      (res: any) => {
        if (res !== undefined) {
          console.log(res);

          this.sweetAlertService.success(res);
        }
      }, (err: any) => {
        this.sweetAlertService.error(err);
      }, () => {
        this.resetFormValidator(),
          this.getList();
        this.getScreenerAndReviewerForList();
      }
    )
  }

  getScreenerReviewerPair(): void {
    this.apiService.findById(ENDPOINTS.screenerReviewerPairs, this.formListComponent.idForUpdate).subscribe((res: any) => {
      if (res) {
        this.screenerReviewerPair = res.responseData.data;
        console.log(this.screenerReviewerPair);

        console.log(this.screenerList.filter((res1) => res1.username === this.screenerReviewerPair.screener.username)[0]);
        console.log(this.screenerReviewerPair.screener);
      }
    });
  }

  update(): void {
    this.apiService.update(ENDPOINTS.screenerReviewerPairs, this.screenerReviewerPair).subscribe(
      (res: any) => {
        if (res !== undefined) {
          this.sweetAlertService.success(res);
        }
      }, (err: any) => {
        this.sweetAlertService.error(err);
      }, () => {
        this.resetFormValidator()
        this.getList();
        this.getScreenerAndReviewerForList();
      });
  }

  setFormValidator(): void {
    this.screenerReviewerPairForm = this.formBuilder.group({
      screenerName: ['', Validators.required],
      reviewerName: ['', Validators.required],
    });
  }

  resetFormValidator(): void {
    this.screenerReviewerPair = new ScreenerReviewerPair();
    this.screenerReviewerPairForm.markAsUntouched();
    this.screenerReviewerPairForm.markAsPristine();
  }

  getScreenerAndReviewerForList() {
    this.apiService.findAll(ENDPOINTS.userAccount).subscribe((res: any) => {
      this.screenerList = res.responseData.data.filter(
        (account: any) => account.userRole.name === USER_ROLES.ROLE_SCREENER && account.status === COMMON_FUNCTION.ACTIVE);
      this.reviewerList = res.responseData.data.filter(
        (account: any) => account.userRole.name === USER_ROLES.ROLE_REVIEWER && account.status === COMMON_FUNCTION.ACTIVE);
    });
  }

  compareUser(value: any, selected: any): boolean {
    if (value && selected) {
      return value.username === selected.username;
    }
  }

  delete() {
    Swal.fire({
      text: 'Are you sure you want to delete this screener reviewer pairing?',
      icon: 'question',
      showCancelButton: true,
      confirmButtonColor: '#3085d6',
      cancelButtonColor: '#d33',
      confirmButtonText: 'Confirm'
    }).then((result) => {
      if (result.value) {
        this.apiService.deleteById(ENDPOINTS.screenerReviewerPairs, this.screenerReviewerPair.id).subscribe(
          () => {
            this.sweetAlertService.customSuccessMessage('Screener reviewer pairing successfully deleted.');
            this.resetFormValidator();
            this.getList();
          }
        );
      }
    });
  }

}
